import React, {Component} from 'react';
import {WebTorrentHelper} from "./WebTorrentHelper";
import {
    Button,
    Checkbox,
    Collapse,
    Container,
    createTheme,
    CssBaseline,
    Divider,
    IconButton,
    InputAdornment,
    List,
    ListItemAvatar,
    ListItemButton,
    ListItemText,
    Snackbar,
    Stack,
    TableCell,
    TableRow,
    TextField,
    ThemeProvider,
    Tooltip,
    Typography
} from "@mui/material";
import {
    AddCircle,
    ContentCopy,
    Delete,
    DeleteForever,
    Download,
    DownloadForOffline,
    Link,
    OndemandVideo,
    PauseCircle,
    PlayCircle,
    PlayCircleOutline,
    Search,
    Upload
} from "@mui/icons-material";
import {copyToClipboard, humanFileSize, toTime} from "./utils";
import {LinearProgressWithLabel} from "./components/LinearProgressWithLabel";

import {Menu} from "./components/Menu";
import AddTorrent from "./components/AddTorrent";
import ConfigDialog from "./components/ConfigDialog";
import TorrentClientTable from "./components/TorrentClientTable";
import FilesTable from "./components/FilesTable";
import {grey} from "@mui/material/colors";
import FileElement from "./components/FileElement";
import GamesList from "./components/GamesList";
import {CLIENT, CLIENT_DOWNLOAD, CLIENT_SEEDING, GAMES, MOVIES, TVSHOW} from "./types";

const defaultTheme = createTheme();
const options = {
    typography: {
        fontSize: 12,
    },
    palette: {
        mode: 'dark',
        background: {
            default: "#303030",
            paper: "#424242"
        }
    },
    components: {
        MuiLinearProgress: {
            styleOverrides: {
                root: {
                    borderRadius: "10px"
                }
            }
        },
        MuiListItemButton: {
            styleOverrides: {
                root: {
                    borderRadius: "20px",
                    paddingLeft: "10px",
                    paddingRight: "10px",
                }
            }
        },
        MuiTableContainer: {
            styleOverrides: {
                root: {
                    height: "100%",
                },
            },
        },
        MuiContainer: {
            styleOverrides: {
                root: {
                    paddingLeft: "0px",
                    paddingRight: "0px",
                    height: "100%",
                    [defaultTheme.breakpoints.up('xs')]: {
                        paddingLeft: "0px",
                        paddingRight: "0px",
                        paddingTop: "5px",
                    }
                },
            },
        },
    },
};

export class WebTorrentGuiV2 extends Component {

    state = {
        theme: createTheme(options),
        selectedTorrent: [],
        torrents: [],
        filterTorrent: (x) => {
            return true
        },
        showAddTorrent: false,
        enabledView: CLIENT,
        configuration: {},
        search: "",
        severity: "success",
        snackbar: false,
        snackbarMessage: "Copied to clipboard",
        defaultMenu: [
            {
                type: GAMES,
                label: "Games",
                tooltip: "A curated list of repacked games"
            }, {
                type: MOVIES,
                label: "Movies",
                tooltip: "Explore a list of movies"
            }, {
                type: TVSHOW,
                label: "Tv"
            },
        ]

    }

    componentDidMount() {
        let {host, port, baseUrl} = this.props
        this.setState({client: new WebTorrentHelper(baseUrl ? {baseUrl} : {baseUrl: host + ":" + port})}, async () => {
            await this.refreshStatus();
        })
        this.interval = setInterval(this.refreshStatus, 5000)
    }

    componentWillUnmount() {
        clearInterval(this.interval)
    }

    refreshStatus = async () => {
        try {
            let {client, filterTorrent} = this.state
            let res = await client.checkStatus();
            let confRes = await client.getConf();
            this.setState({torrents: res.data.filter(filterTorrent), configuration: confRes.data})
        } catch (e) {
            console.error(e)
        }

    }

    removeAll = () => {
        try {
            let {client, torrents, selectedTorrent} = this.state;
            torrents.forEach(x => {
                if (selectedTorrent == null || selectedTorrent.length < 1 || selectedTorrent.includes(x.infoHash)) {
                    client.removeTorrent({magnet: x.magnet})
                }
            })
            this.refreshStatus().catch(console.error)
        } catch (e) {
            console.error(e)
        }
    }

    destroyAll = () => {
        try {
            let {client, torrents, selectedTorrent} = this.state;
            torrents.forEach(x => {
                if (selectedTorrent == null || selectedTorrent.length < 1 || selectedTorrent.includes(x.infoHash)) {
                    client.destroyTorrent({magnet: x.magnet})
                }
            })
            this.refreshStatus().catch(console.error)
        } catch (e) {
            console.error(e)
        }
    }

    resumeAll = () => {
        try {
            let {client, torrents, selectedTorrent} = this.state;
            torrents.forEach(x => {
                if (selectedTorrent == null || selectedTorrent.length < 1 || selectedTorrent.includes(x.infoHash)) {
                    client.addTorrent({magnet: x.magnet})
                }
            })
            this.refreshStatus().catch(console.error)
        } catch (e) {
            console.error(e)
        }
    }
    pauseAll = () => {
        try {
            let {client, torrents, selectedTorrent} = this.state;

            torrents.forEach(x => {
                if (selectedTorrent == null || selectedTorrent.length < 1 || selectedTorrent.includes(x.infoHash)) {
                    client.pauseTorrent({magnet: x.magnet})
                }
            })
            this.refreshStatus().catch(console.error)
        } catch (e) {
            console.error(e)
        }
    }

    openSnackbar = () => {
        this.setState({snackbar: true})
    }

    renderBody = () => {
        let {enabledView, client, torrents, search, selectedTorrent} = this.state;
        let {remote} = this.props;
        switch (enabledView) {
            case MOVIES:
                return <FilesTable
                    key={"FILES_TABLE_" + enabledView.toString()}
                    client={client}
                    torrents={torrents}
                    search={search}
                    searchApi={client.searchMovie}
                    navigateBack={() => {
                        this.setState({
                            filterTorrent: (x) => {
                                return true;
                            },
                            enabledView: CLIENT,
                            search: ""
                        }, this.refreshStatus)
                    }}
                />
            case GAMES:
                return <GamesList
                    client={client}
                    torrents={torrents}
                    search={search}
                    navigateBack={() => {
                        this.setState({
                            filterTorrent: (x) => {
                                return true;
                            },
                            enabledView: GAMES,
                            search: ""
                        }, this.refreshStatus)
                    }}
                />
            case TVSHOW:
                return <FilesTable
                    key={"FILES_TABLE_" + enabledView.toString()}
                    client={client}
                    torrents={torrents}
                    search={search}
                    searchApi={client.searchTv}
                    navigateBack={() => {
                        this.setState({
                            filterTorrent: (x) => {
                                return true;
                            },
                            enabledView: CLIENT,
                            search: ""
                        }, this.refreshStatus)
                    }}
                />
            case CLIENT:
            case CLIENT_DOWNLOAD:
            case CLIENT_SEEDING:
            default:
                return <TorrentClientTable
                    search={search}
                    onClick={(event) => {
                        if (event.target.checked) {
                            this.setState({selectedTorrent: torrents.map(x => x.infoHash)})
                        } else {
                            this.setState({selectedTorrent: []})
                        }
                    }}
                    torrents={torrents}
                    predicate={x => selectedTorrent.includes(x.infoHash)}
                    callbackfn={torrent => {
                        let videoFiles = [];
                        torrent.files.forEach(f => {
                            if (f.mime && f.mime.includes("video")) {
                                videoFiles.push(f)
                            }
                        })
                        let state;
                        let color = "primary";
                        if (torrent.paused) {
                            state = "paused";
                            color = "warning";
                        } else if (torrent.progress == 1) {
                            state = "completed";
                            color = "success";
                        } else if (torrent.timeRemaining > 0) {
                            state = toTime(torrent.timeRemaining)
                        } else {
                            state = "--:--"
                        }
                        let size = 0;
                        torrent.files.forEach(file => {
                            size = size + file.length
                        })
                        let isRowSelected = this.isRowSelected(torrent.infoHash)
                        return (<>
                            <TableRow key={torrent.infoHash} sx={{borderBottom: 'unset'}}>
                                <TableCell padding={"checkbox"} component="th" scope="row">
                                    <Checkbox
                                        color="primary"
                                        checked={isRowSelected}
                                        onClick={() => this.onChangeRowSelection(torrent.infoHash)}
                                    />
                                </TableCell>
                                <TableCell component="th" scope="row">
                                    <Typography variant={"body2"}>{torrent.name}</Typography>
                                </TableCell>
                                <TableCell align="right">
                                    <LinearProgressWithLabel color={color}
                                                             value={torrent.progress * 100}/>
                                </TableCell>
                                <TableCell align="left">
                                    <Typography variant={"body2"}>
                                        {state}
                                    </Typography>
                                </TableCell>
                                <TableCell align="left">
                                    <Typography variant={"body2"}>
                                        {humanFileSize(size)}
                                    </Typography>
                                </TableCell>
                                <TableCell align="right">
                                    {videoFiles.length > 0 &&
                                        <Tooltip title={videoFiles.length === 1 ? "Reproduce video file" :
                                            <List sx={{maxWidth: "200px", maxHeight: "400px", overflow: "auto"}}>
                                                {videoFiles.map((file, index) => {
                                                    return <ListItemButton key={"TO_PLAY_ELEMENT_" + index}
                                                                           onClick={() => {
                                                                               if (remote) {
                                                                                   let a = document.createElement("a");
                                                                                   a.href = client.fileStreamLink(file.id, file.name, remote);
                                                                                   a.download = file.name;
                                                                                   a.click();
                                                                               } else {
                                                                                   client.fileOpen(file.id);
                                                                               }
                                                                           }
                                                                           }
                                                    >
                                                        <ListItemAvatar>
                                                            <Stack alignItems={"center"} justifyContent={"center"}>
                                                                {`${Math.round(
                                                                    file.progress * 100,
                                                                )}%`}
                                                                <OndemandVideo/>
                                                            </Stack>
                                                        </ListItemAvatar>
                                                        <ListItemText
                                                            primary={file.name}
                                                        />
                                                    </ListItemButton>
                                                })}
                                            </List>}>
                                            <IconButton onClick={() => {
                                                if (videoFiles.length === 1) {
                                                    let file = videoFiles[0]
                                                    if (remote) {
                                                        let a = document.createElement("a");
                                                        a.href = client.fileStreamLink(file.id, file.name, remote);
                                                        a.download = file.name;
                                                        a.click();
                                                    } else {
                                                        client.fileOpen(file.id);
                                                    }
                                                }
                                            }}>
                                                <PlayCircleOutline color={"primary"}/>
                                            </IconButton>
                                        </Tooltip>}
                                    <Tooltip title={"Download torrent file"}>
                                        <IconButton onClick={() => {
                                            let a = document.createElement("a");
                                            a.href = client.getTorrentFile(torrent.infoHash, torrent.name + ".torrent", remote);
                                            a.download = torrent.name + ".torrent";
                                            a.click();
                                        }}>
                                            <DownloadForOffline color={"primary"}/>
                                        </IconButton>
                                    </Tooltip>
                                    <Tooltip title={"Copy a link to share with friends!"}>
                                        <IconButton onClick={() => {
                                            copyToClipboard("https://tndsite.gitlab.io/quix-player/?magnet=" + torrent.infoHash, this.openSnackbar)
                                            // copyToClipboard("https://btorrent.xyz/download#" + torrent.infoHash, this.openSnackbar)
                                        }}>
                                            <Link color={"primary"}/>
                                        </IconButton>
                                    </Tooltip>
                                    <Tooltip title={"Copy magnet to the clipboard"}>
                                        <IconButton onClick={() => {
                                            copyToClipboard(torrent.magnet, this.openSnackbar)
                                        }}>
                                            <ContentCopy color={"primary"}/>
                                        </IconButton>
                                    </Tooltip>
                                </TableCell>
                            </TableRow>
                            <TableRow key={"Secondary-" + torrent.infoHash}>
                                <TableCell padding={"checkbox"} sx={{paddingBottom: 0, paddingTop: 0}}
                                           colSpan={6}>
                                    <Collapse in={isRowSelected} timeout="auto" unmountOnExit>
                                        {torrent.files.map(f => {
                                            return <FileElement key={"file-" + f.id}
                                                                torrentMagnet={torrent.magnet}
                                                                remote={remote} file={f} client={client}/>
                                        })}
                                    </Collapse>
                                </TableCell>
                            </TableRow>
                        </>)
                    }}/>
        }
    }


    render() {
        let {
            client,
            theme,
            configuration,
            showAddTorrent,
            showConfig,
            enabledView,
            search,
            severity,
            snackbar,
            snackbarMessage,
            defaultMenu
        } = this.state;
        let {logo} = this.props;
        let {downloadSpeed, downloadPath, uploadSpeed, actualUpload, actualDownload} = configuration;
        return (
            <ThemeProvider theme={theme}>
                <CssBaseline enableColorScheme/>
                <Container maxWidth="false">
                    <Snackbar
                        anchorOrigin={{vertical: "top", horizontal: "right"}}
                        open={snackbar}
                        onClose={() => {
                            this.setState({snackbar: false})
                        }}
                        severity={severity}
                        message={snackbarMessage}
                        key={"snackabr"}
                        autoHideDuration={5000}
                    />
                    <Stack
                        sx={{height: "100%"}}
                        direction={"row"}>
                        <Menu
                            defaultMenu={defaultMenu}
                            logo={logo}
                            enabledView={enabledView}
                            changeView={(enabledView) => {
                                this.setState({
                                    enabledView
                                })
                            }}
                            onChange={this.darkLightMode}
                            openSettings={this.openSettings}
                            filterDownload={() => {
                                this.setState({
                                    filterTorrent: (x) => {
                                        return x.paused === false && x.progress !== 1;
                                    },
                                    enabledView: CLIENT_DOWNLOAD
                                }, this.refreshStatus)
                            }}
                            filterSeeding={() => {
                                this.setState({
                                    filterTorrent: (x) => {
                                        return x.paused === false && x.progress >= 1;
                                    },
                                    enabledView: CLIENT_SEEDING
                                }, this.refreshStatus)
                            }}
                            filterHome={() => {
                                this.setState({
                                    filterTorrent: (x) => {
                                        return true;
                                    },
                                    enabledView: CLIENT
                                }, this.refreshStatus)
                            }}
                        />
                        <Divider orientation={"vertical"}/>
                        <Stack sx={{width: "100%"}} direction={"column"}>
                            <Stack sx={{width: "100%", padding: "5px"}} alignItems={"center"} spacing={1}
                                   direction={"row"}>
                                <Button size={"small"} color={"primary"} variant={"contained"}
                                        startIcon={<AddCircle/>} onClick={() => {
                                    this.setState({showAddTorrent: true})
                                }}>Add</Button>
                                <Button size={"small"} color={"primary"} variant={"contained"}
                                        startIcon={<PauseCircle/>} onClick={this.pauseAll}>pause</Button>
                                <Button size={"small"} color={"primary"} variant={"contained"}
                                        startIcon={<PlayCircle/>} onClick={this.resumeAll}>Resume</Button>
                                <Tooltip title={"Remove element only from the list"}>
                                    <Button size={"small"} color={"primary"} variant={"contained"}
                                            startIcon={<Delete/>} onClick={this.removeAll}>Remove</Button>
                                </Tooltip>
                                <Tooltip title={"Remove element from list and from the memory"}>
                                    <Button size={"small"} color={"primary"} variant={"contained"}
                                            startIcon={<DeleteForever/>} onClick={this.destroyAll}>Delete</Button>
                                </Tooltip>
                                {/*TODO Enable when sorting is working                                */}
                                {/*<Divider orientation={"vertical"}/>*/}
                                {/*<IconButton disabled color={"primary"}><KeyboardArrowUp/></IconButton>*/}
                                {/*<IconButton disabled color={"primary"}><KeyboardArrowDown/></IconButton>*/}
                                {![CLIENT, CLIENT_DOWNLOAD, CLIENT_SEEDING].includes(enabledView) && <><Divider
                                    orientation={"vertical"}/>
                                    <TextField size={"small"} variant={"outlined"} label={"Search"}
                                               InputProps={{
                                                   startAdornment: (
                                                       <InputAdornment position="start">
                                                           <Search/>
                                                       </InputAdornment>
                                                   )
                                               }}
                                               value={search}
                                               onChange={(e) => {
                                                   let text = e.target.value;
                                                   this.setState({search: text})
                                               }}
                                    /></>}
                                <Typography
                                    sx={{display: "flex", alignItems: "center"}}
                                    variant="body2"
                                >
                                    <Download
                                        fontSize="small"/>{humanFileSize(actualDownload) + "/s"} {humanFileSize(actualUpload) + "/s"}<Upload
                                    fontSize="small"/><br/>
                                </Typography>
                            </Stack>
                            <Divider/>
                            {this.renderBody()}
                        </Stack>
                    </Stack>
                    <AddTorrent
                        open={showAddTorrent}
                        onSubmit={(path, magnet) => {
                            client.addTorrent({magnet, path})
                                .then(this.refreshStatus)
                                .catch(console.error)
                            this.setState({showAddTorrent: false})
                        }}
                        onClose={() => {
                            this.setState({showAddTorrent: false})
                        }}
                    />
                    <ConfigDialog
                        open={showConfig}
                        key={"MODAL: " + downloadSpeed + downloadPath + uploadSpeed}
                        onSubmit={(configuration) => {
                            client.saveConf(configuration)
                            this.setState({showConfig: false})
                        }}
                        onClose={() => {
                            this.setState({showConfig: false})
                        }}
                        downloadPath={downloadPath}
                        downloadSpeed={downloadSpeed}
                        uploadSpeed={uploadSpeed}
                    />
                </Container>
            </ThemeProvider>
        );
    }


    isRowSelected = (infoHash) => {
        let {selectedTorrent} = this.state;
        return selectedTorrent.includes(infoHash);
    }
    onChangeRowSelection = (infoHash) => {
        let {selectedTorrent} = this.state;
        if (this.isRowSelected(infoHash)) {
            this.setState({
                selectedTorrent: selectedTorrent.filter(x => x != infoHash)
            })
        } else {
            selectedTorrent.push(infoHash)
            this.setState({
                selectedTorrent
            })
        }
    }


    openSettings = () => {
        this.setState({showConfig: true})
    }
    darkLightMode = (e, checked) => {
        let mode = checked ? "dark" : "light";
        let background = {
            default: "#303030",
            paper: "#424242"
        }
        if (mode == "light") {
            background = {
                default: grey[300],
                paper: grey[200]
            }
        }
        this.setState({
            theme: createTheme({
                ...options,
                palette: {
                    ...options.palette,
                    mode,
                    background
                }
            })
        })
    }
}
